.container {

    display: grid;
    grid-template-columns: 100%;

}

.item {

    grid-row: 1 / 2;
    grid-column: 1 / 2;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;

    padding: 1rem 1.5rem;
    z-index: 10;
    /* height: 5rem; */

}

/* .item_invisible {

    z-index: 1;

} */

.hoverComponent {

    grid-row: 1 / 2;
    grid-column: 1 / 2;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;

    background-color: white;

    z-index: 10;

    visibility: visible;
    opacity: .8;

    -webkit-transition: all .5s;

    -o-transition: all .5s;

    transition: all .5s;

}


.hoverComponent .button,
.hoverComponent .button:active,
.hoverComponent .button:focus {

    display: block;
    width: 5rem;
    height: 3rem;
    padding: 0 .8rem;

    border: none;
    border-radius: 5px;

    background-color: #ff7575;

    font-family: 'Quicksand', sans-serif;
    font-size: 1rem;

    cursor: pointer;

}

.thumbnail {

    display: block;
    width: 5rem;
    height: 4rem;

    border-radius: 10%;

}

.info {

    display: -ms-flexbox;

    display: flex;

    -ms-flex-direction: column;

        flex-direction: column;
    

    font-family: 'Quicksand', sans-serif;

}


