.container {

    display: -ms-flexbox;

    display: flex;

    -ms-flex-pack: center;

        justify-content: center;
    -ms-flex-align: center;
        align-items: center;

    padding: 1rem 1rem;
    border-radius: 5px;

    -webkit-transition: all .2s;

    -o-transition: all .2s;

    transition: all .2s;

    cursor: pointer;

}

.container:hover {

    background-color: #ffe5a1;

}

.container .svg {

    display: block;
    width: 3rem;
    height: auto;

}

.container .svg svg {

    display: block;
    width: 100%;
    height: 100%;
    fill: #d4227b;

}

.container .info {

    margin-left: 2rem;

    font-family: 'Quicksand', sans-serif;

}




