.container {

    display: -ms-flexbox;

    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;

    margin: 0 2rem;
    padding: 1rem;

    border-radius: 5px;

    cursor: pointer;

    -webkit-transition: all .2s;

    -o-transition: all .2s;

    transition: all .2s;

}

.container:hover {

    background-color: #ffe5a1;

}

.container img {

    display: block;
    -o-object-fit: cover;
       object-fit: cover;

    width: 25%;
    height: 100%;

    border-radius: 10%;

}

.container h2 {

    display: block;
    font-family: 'Quicksand', sans-serif;

}


